import '../styles/index.css';
import dominicanFlag from '../assets/images/dominicanflag.jpeg';
import { homePage } from './home';

function contentElement() {
    const contentDiv = document.querySelector("#content");
    return {
        contentDiv
    }

};

function bodyContent() {
    const mainEl = document.createElement('main');
    mainEl.setAttribute('id',"tab-content");
    return {
        mainEl
    }
};

function tabsController() {
    const tabContentArr = ['home', 'menu', 'contact'];

    const tabController = document.createElement('ul');
    
    tabController.setAttribute("role","tabslist");

    const tabElements = tabContentArr.map(name => {
        return `<li data-visible=${false} data-name="${name}" class="tab-item text-red fs-500">${name}</li>`
    });

    tabController.innerHTML = tabElements.join('');

    return {
        tabList: [...document.querySelectorAll('[data-visible]')],
        tabContentArr,
        tabController,
        tabElements
    }

}


