import dominicanFlag from '../assets/images/dominicanflag.jpeg';

export function homePage() {
    const createPage = () => { return `<div data-name="home">
    <h1>Welcome to the Dominican Restaurant</h1>
    <img src="${dominicanFlag}" alt="Dominican Flag" />
    <div>
        <h3>We Serve Authentic Dominican Food</h3>
        <span>Check out our menu</span>
    </div>
    </div>`
    }

    const contentDiv = document.createElement('div');
    contentDiv.setAttribute("data-name", 'home');
    contentDiv.innerHTML=` <h1>Welcome to the Dominican Restaurant</h1>
    <img src=${dominicanFlag} alt="Dominican Flag" />
    <div>
        <h3>We Serve Authentic Dominican Food</h3>
        <span>Check out our menu</span>
    </div>`

    return {
        createPage,
        content: contentDiv
    }
}